.accordion{
    width:80%;
    margin:0 auto;

    @include respond(tab-land) {
       width:90%;
    
        
        }

    &__heading{
        text-align: center;
    }
    &__container{
        display: flex;
        justify-content: space-between;
        align-items: center;

       

            @include respond(tab-port) {
              
                flex-direction: column;
                
                }

                @include respond(phone) {
                  
                    
                    }
    }

    &__col1{
        width:45%;

        @include respond(tab-port) {
            width:60%;
          margin-bottom: 8rem;
            
            }

            @include respond(phone) {
                width:80%;
              
                
                }
    }

    &__col2{
        width:50%;

        @include respond(tab-port) {
            width:80%;
       
            
            }

            @include respond(phone) {
                width:90%;
               
                
                }
       
        
       
    }

    &__image__box{
        width:100%;
        height:60vh;
        position: relative;
    }

    &__image{
        width:100%;
        height:100%;

        &__info{
            @include absCenter;
            color:white;

           
        
            
        }
    }


    &__mobile__icon{
        color:white;
    }

    &__headings{
        font-size: 3rem;
        margin:1rem 0rem;
        font-family: $font-secondary;

        @include respond(phone){
            font-size: 2rem;
        }
    }

    &__text{
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }
   
    &__main__secondary{
        font-size: 3rem;
        font-weight: 900;
        color:#3b3663;
        margin-bottom: 2rem;
        font-family: $font-secondary;

        @include respond(phone){
            font-size: 2rem;
        }

    }
}


.ui-accordion{

    &-content{
        font-size: 1.3rem;
        color: #696687;
        
        font-weight: 400;
        font-family: "Rubik";
        
    }

   
    }
    
#ui-id-1,#ui-id-3,#ui-id-5,#ui-id-7,#ui-id-9{
    font-size: 1.5rem;
    padding:0.5rem 1.5rem;
    font-family: $font-secondary;
}

#ui-id-2,#ui-id-4,#ui-id-6,#ui-id-8,#ui-id-10{
    font-size: 1.2rem;
}

.ui-accordion-icons{
    display: none;
}
    
    .ui-accordion .ui-accordion-header-active{
        background-image: linear-gradient(20deg, #ff841f 0%, #ff9721 57%) !important;
        border: none !important;
    }

   

        .ui-accordion-content{
            font-size: 1.3rem;
            color: #696687;
            
            font-weight: 400;
            font-family: "Rubik";
            
        }
        