.hero{

    &::after{
        content:"SEO";
        color:#fafafa;
        position:absolute;
        top:30%;
        left:-13%;
        font-size:20rem;
        transform: rotate(-90deg);
        font-family: 'Rubik', sans-serif;
        font-weight: 900;
        letter-spacing: 0.5rem;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: #fff;
        z-index: -10;
    }
    &__heading{
        z-index: 11;

        
        &__primary{
            color:$purple-color;
            font-family: $font-secondary;
        }

        &__type{
            font-size: 6rem;
            font-weight: 800;
            margin-bottom: 1rem;
            color:$purple-color;
            font-family: $font-secondary;
            line-height: 1;

            @include respond(phone){
                font-size: 4rem;
            }
        }

        &__main{
            font-size: 6rem;
            font-weight: 800;
            color:$purple-color;
            font-family: $font-secondary;
            line-height: 1;
            @include respond(phone){
                font-size: 4rem;
            }
           
        }

        &__text{
            font-size: 1.5rem;
            margin-bottom: 2rem;
            color:$purple-color;
            font-family: $font-primary;

            @include respond(phone){
                font-size: 1.2rem;
            }
        }
    }

    &__container{
        display: flex;
        position: relative;
      
    }

    &__col1{
     
        position:absolute;
        top:30%;
        left:10%;
    }

    &__col2{
        background-image: url("../../images/header-bg.jpg");
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        height:100vh;
        width:75%;
        margin-left: auto;

        @include respond(tab-port){
          width:100%;
        }


       
    }

    &__icon{
       width:1rem;
       height:1rem;
    }

    &__corner__image{
        // position:absolute;
        // top:90%;
        margin-top: -5rem;

        @include respond(phone){
            margin-top: 0rem;
            height:20rem;
        }
      }
}